












import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class InstallInstruction extends Vue {
  @Prop({ default: "$", required: false })
  private prompt!: string;

  @Prop({ required: true })
  private command!: string;

  @Prop({ required: true })
  private os!: string;

  @Prop({ required: true })
  private type!: string;
}
